  }
  .rt {
    border: thick double #32a1ce;
    width: auto;
  }
  .content {
    border: 2px solid darkblue;
    border-radius: 7px;
    min-height: 50px;
    max-width: 100%;
    height: auto;
    position: absolute;
    z-index: 12;
    margin-top: 10px;
    margin-right: 8px;
    min-width: 375px;
  }
  .shine-button {
    text-decoration: none;
    display: inline-block;
    padding: 10px 30px;
    margin: 10px 20px;
    position: relative;
    overflow: hidden;
    border: 2px solid #fe6637;
    background-color: #f1daa5;
    border-radius: 8px;
    font-family: "Source Code Pro", monospace;
    color: black;
    transition: 0.2s ease-in-out;
  }
  .shine-button:before {
    content: "";
    background: linear-gradient(
      90deg,
      rgba(255, 255, 255, 0.1),
      rgba(255, 255, 255, 0.5)
    );
    height: 50px;
    width: 50px;
    position: absolute;
    top: -8px;
    left: -75px;
    transform: skewX(-45deg);
  }
  .shine-button:hover {
    background: #fe6637;
    color: #fff;
  }
  .shine-button:hover:before {
    left: 150px;
    transition: 0.5s ease-in-out;
  }
